section#home {
  margin: 0;
  background-color: $default-color;
  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  color: $default-text-color;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;

}

section#homeContent {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  color: $default-text-color;

  background: radial-gradient(circle at center, rgba(255,255,255, 0) 0, rgba(0,0,0,0.2) 100%);
}

a.whistle-button {
  font-size: 0.7rem;
  color: $whistle-color;
  background-color: rgba(255, 255, 255, 0.7);

  text-decoration: none;

}

.stressed-btn {
  font-size: 1.2rem;
}

.stressed-btn-fw {
}

.stressed-btn-fh {
}

.helpdesk {
  position: fixed;
  left: 100px;
  bottom: 30px;
}

@media (max-width: 767.98px) { // Mobile
  .company-logo {
    width: 100px;

    font-size: 1.5rem;
    font-weight: bold;
  }

  .company-hrms-logo {
    width: 70px;
  }

  .company-hrms-logo, .company-logo {
    img {
      width: 100%;
    }
  }

  #closeButtonMobile {
    cursor: pointer;
  }

  #callToActionBtnMobile,
  .whistle-button,
  .helpdesk {
    position: fixed;
  }

  #callToActionBtnMobile, .whistle-button {
    left: 50%;
    transform: translateX(-50%) !important;
  }

  #callToActionBtnMobile {
    bottom: 80px;
    background-color: $default-btn-color !important;
    color: #ffffff;
  }

  .whistle-button, .helpdesk {
    bottom: 30px;
  }

}

@media (min-width: 768px) { // Desktop

  .company-logo {
    position: fixed;
    left: 3rem;
    top: 2.3rem;
    font-size: 2.5rem;
    font-weight: bold;
  }

  #closeButton {
    position: absolute;
    top: 20px;
    left: 20px;
    cursor: pointer;
  }

  #callToActionBtn,
  .whistle-button,
  .helpdesk {
    position: fixed;
  }

  #callToActionBtn, .whistle-button {
    right: 3rem;
  }

  #callToActionBtn {
    bottom: 80px;
    background-color: $default-btn-color;
    color: #ffffff;
  }

  .whistle-button, .helpdesk {
    bottom: 30px;
  }

}

#loginFormMobile {
  display: none;
}

#loginForm {
  display: none;

  position: fixed;
  right: 5.8rem;
  top: 5rem;

  color: #505050;

  width: 450px;
  max-height: 80vh;

  background-color: rgba(255, 255, 255, 0.95);
  padding: {
    top: 30px;
    bottom: 30px;

    left: 35px;
    right: 35px;
  }

  div.form-wrapper {
    padding: {
      top: 20px;
      bottom: 20px;
    }

    height: 45vh;

    @extend .noscrollbar-scrollable-y-section;

    .form-group {
      margin-bottom: 30px;
    }
  }

  header.login-form {
    margin: {
      top: 25px;
      bottom: 25px;
    }

    h1 {
      font-weight: bold;
      font-size: 2.5rem;
    }
  }

}

#homeContentMobile {
  padding: 0;
  margin: 0;

  height: 100vh;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
}

#homeContentMobile, #homeContent {

  input.form-control {
    border-style: none none solid none;
    border-color: #777777;
    color: #888888;
    border-radius: 0;
    background-color: inherit;
    height: 40px;
    padding: {
      left: 0;
      right: 0;
    }

    &::placeholder {
      color: #888888;
    }
    &:focus {
      -webkit-box-shadow: none;
      -moz-box-shadow: none;
      box-shadow: none;
    }
  }

}

.company-hrms {
  position: fixed;
  right: 2.5rem;
  top: 2rem;
  z-index: 9;
}

.welcome-heading {
  .welcome-prefix {
    letter-spacing: 0.27rem;
    text-transform: uppercase;
  }
}

.noscrollbar-scrollable-y-section {
  overflow-y: auto;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    width: 0 !important;
    background: transparent; /* make scrollbar transparent */
    -ms-overflow-style: none;
    display: none;
    overflow: -moz-scrollbars-none;
    -webkit-appearance: none;
    z-index: -99;
  }
}

.sign-in-btn, .form-label {
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-weight: bold;
}

.sign-in-btn {
  background-color: $default-btn-color;
  color: white;
  margin: {
    top: 35px;
    bottom: 20px;
  }
}

.backstretch {
  background-color: $default-color;
}
